import React, { useState } from 'react';
import styled, { css } from 'styled-components';
import Logo from '../../assets/Logo.png';
import Link from '../Atoms/Link';
import { HashLink } from 'react-router-hash-link';
import { GiHamburgerMenu as HamburgerLogo } from 'react-icons/gi';
import HamburgerWrapper from '../Organisms/HamburgerWrapper';
import AppContext from '../../context';

const Container = styled.div`
  position: absolute;
  height: 15vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: ${({ theme }) => theme.fontFamily.Oswald};
  font-size: ${({ theme }) => theme.fontSize.l};
  color: white;
  transition: 0.3s;

  ${({ navbarresize, hamburger }) =>
    navbarresize &&
    !hamburger &&
    css`
      justify-content: space-evenly;
      z-index: 999;
      height: 7vh;
      position: fixed;
      font-size: ${({ theme }) => theme.fontSize.m};
      background-color: black;
    `}
  ${({ hamburger }) =>
    hamburger &&
    css`
      height: 50vh;
    `}
`;

const LogoContainer = styled.div`
  flex-basis: 30%;

  ${({ navbarresize }) =>
    navbarresize &&
    css`
      display: none;
    `}
`;

const LogoIcon = styled.img`
  margin: 20px 0 0 10px;
  width: 180px;

  @media (max-height: 770px) {
    margin-top: 40px;
  }
  @media (max-height: 560px) {
    margin-top: 60px;
  }
`;

const LinkContainer = styled.div`
  flex-basis: 50%;
  display: flex;
  justify-content: space-evenly;

  @media (max-width: 1090px) {
    flex-basis: 70%;
  }

  ${({ navbarresize }) =>
    navbarresize &&
    css`
      justify-content: center;
      flex-basis: 100%;
    `}
`;

const LinkChild = styled(Link)`
  color: white;
  text-decoration: none;
  transition: all 0.3s;

  :hover {
    color: ${({ theme }) => theme.mainBlue};
    transform: scale(1.04);
  }

  @media (max-width: 750px) {
    display: none;
  }
`;

const Hamburger = styled(HamburgerLogo)`
  display: none;
  margin: 10px 20px 0 0;
  position: relative;
  right: -35%;

  ${({ navbarresize }) =>
    navbarresize &&
    css`
      margin: 0;
      right: 0;
    `}

  @media(max-width:750px) {
    display: block;
  }
`;

const Navbar = () => {
  const [navBar, changeNavBar] = useState(false);
  const [hamburgerOpen, changeHamburgerOpen] = useState(false);

  const NavBarResize = () => {
    window.scrollY >= 80 ? changeNavBar(true) : changeNavBar(false);
  };

  window.addEventListener('scroll', NavBarResize);

  return (
    <Container
      navbarresize={navBar ? 'true' : null}
      hamburger={hamburgerOpen ? 'true' : null}
    >
      <LogoContainer navbarresize={navBar ? 'true' : null}>
        <LogoIcon src={Logo} />
      </LogoContainer>

      {hamburgerOpen && (
        <>
          <AppContext.Provider
            value={[hamburgerOpen, changeHamburgerOpen]}
          >
            <HamburgerWrapper />
          </AppContext.Provider>
        </>
      )}
      <LinkContainer change={navBar ? 'true' : null}>
        <Hamburger
          navbarresize={navBar ? 'true' : null}
          onClick={() => changeHamburgerOpen(true)}
        />
        <LinkChild as={HashLink} smooth to="/#">
          Home
        </LinkChild>
        <LinkChild as={HashLink} smooth to="/#services">
          Services
        </LinkChild>
        <LinkChild as={HashLink} smooth to="/#calculator">
          Calculator
        </LinkChild>
        <LinkChild as={HashLink} smooth to="/#contact">
          Contact
        </LinkChild>
      </LinkContainer>
    </Container>
  );
};

export default Navbar;
