import React from 'react';
import styled from 'styled-components';
import Line from '../Atoms/Line';
import Link from '../Atoms/Link';

const Container = styled.div`
  height: 420px;
  width: 100%;
  font-family: ${({ theme }) => theme.fontFamily.Oswald};
`;
const Footer1 = styled.div`
  background-color: ${({ theme }) => theme.footer.first};
  height: 70%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
`;
const Footer1Text = styled.div`
  text-align: left;
  font-size: ${({ theme }) => theme.fontSize.xs};
  flex-basis: 25%;
  justify-items: center;
  color: white;

  @media (max-width: 706px) {
    font-size: ${({ theme }) => theme.fontSize.xxs};
  }
  @media (max-width: 470px) {
    font-size: 12px;
  }
`;
const Footer1Title = styled.div`
  color: ${({ theme }) => theme.mainBlue};
  font-size: ${({ theme }) => theme.fontSize.m};

  @media (max-width: 706px) {
    font-size: ${({ theme }) => theme.fontSize.s};
  }
`;

const Footer2 = styled(Footer1)`
  background-color: ${({ theme }) => theme.footer.second};
  justify-content: center;
  height: 20%;
`;

const Footer2Text = styled(Footer1Text)`
  flex-basis: 20%;
  color: white;
  text-align: center;
  margin: 0;
  font-size: ${({ theme }) => theme.fontSize.s};

  @media (max-width: 470px) {
    flex-basis: 25%;
    font-size: ${({ theme }) => theme.fontSize.xs};
  }
`;

const Footer3 = styled(Footer1)`
  justify-content: space-around;
  background-color: ${({ theme }) => theme.footer.third};
  height: 10%;
`;

const Footer3Text = styled(Footer1Text)`
  text-align: center;
  color: ${({ theme }) => theme.darkGrey};
`;

const Footer = () => (
  <>
    <Line />
    <Container>
      <Footer1>
        <Footer1Text>
          <Footer1Title>Abous Us</Footer1Title>
          Lorem ipsum dolor sit amet, consectetur adipiscing
          elit, sed do eiusmod tempor incididunt ut labore
          et dolore magna aliqua. Ut enim ad minim veniam,{' '}
        </Footer1Text>
        <Footer1Text>
          <Footer1Title>Free Quote</Footer1Title>
          Lorem ipsum dolor sit amet, consectetur adipiscing
          elit, sed do eiusmod tempor incididunt ut labore
          et dolore magna aliqua. Ut enim ad minim veniam,{' '}
        </Footer1Text>
        <Footer1Text>
          <Footer1Title>Contact</Footer1Title>
          SolarSystem <div> Nowa 1 99 - 999 Warszawa</div>
          <div> biuro@solarsystem.pl </div>
          888 - 8888 - 888
        </Footer1Text>
      </Footer1>
      <Footer2>
        <Footer2Text>
          <Link name="footer" path="#" title="Home" />
        </Footer2Text>
        <Footer2Text>
          <Link
            name="footer"
            path="#services"
            title="Services"
          />
        </Footer2Text>
        <Footer2Text>
          <Link
            name="footer"
            path="#calculator"
            title="Calculator"
          />
        </Footer2Text>
        <Footer2Text>
          <Link
            name="footer"
            path="#contact"
            title="Contact"
          />
        </Footer2Text>
      </Footer2>
      <Footer3>
        <Footer3Text>Jakub Imiołczyk</Footer3Text>
        <Footer3Text>@2021 SolarSystem</Footer3Text>
      </Footer3>
    </Container>
  </>
);

export default Footer;
