import React from 'react';
import styled from 'styled-components';
import Heading from '../Atoms/Heading';
import FormInput from '../Atoms/FormInput';
import Buttton from '../Atoms/Button';

const Container = styled.div`
  width: 610px;
  height: 750px;
  box-shadow: 0px 0px 21px 10px rgba(0, 0, 0, 0.25);
  display: grid;
  grid-template-rows: 0.2fr 0.4fr 1.5fr 0.1fr;
  justify-items: center;
  padding: 20px;
  margin: 20px;
  background-color: white;

  @media (max-width: 650px) {
    width: 510px;
    height: 610px;
  }

  @media (max-width: 546px) {
    width: 360px;
  }
  @media (max-width: 400px) {
    width: 300px;
  }
  @media (max-width: 330px) {
    margin: 0;
  }
`;

const Text = styled.span`
  font-size: ${({ theme }) => theme.fontSize.xxs};
  font-family: ${({ theme }) => theme.fontFamily.Roboto};
  color: ${({ theme }) => theme.darkGrey};
  font-weight: 500;
  align-self: center;
  text-align: center;

  padding: 10px 0;

  @media (max-width: 546px) {
    font-size: 13px;
  }
`;

const Inputs = styled.div`
  width: 100%;
  display: grid;
  grid-gap: 10px;
  grid-template-rows: repeat(1fr, 3);
`;

const InputsMiddle = styled.div`
  display: flex;
  justify-content: space-between;
`;

const Form = () => (
  <>
    <Container data-aos="fade-up">
      <Heading>Contact Us</Heading>
      <Text>
        <div>
          Did not find everything you were looking for on the website?
        </div>
        <div> Have a question about another issue?</div>
        Write us a message or call us. We will try to dispel any
        doubts.
      </Text>
      <Inputs>
        <FormInput width="100%" placeholder="name and surname" />
        <InputsMiddle>
          <FormInput width="60%" placeholder="mail adres" />
          <FormInput
            width="35%"
            placeholder="phone number"
            type="tel"
          />
        </InputsMiddle>
        <FormInput type="textarea" placeholder="Message" />
      </Inputs>
      <Buttton send />
    </Container>
  </>
);

export default Form;
