import React from 'react';
import ServiceWrapper from '../components/Organisms/ServiceWrapper';
import styled from 'styled-components';
import Line from '../components/Atoms/Line';
import ManufacturerWrapper from '../components/Organisms/ManufacturerWrapper';
import ServicePhoto1 from '../assets/ServicePhoto1.png';
import ServicePhoto2 from '../assets/ServicePhoto2.png';
import Ocean from '../assets/Ocean.svg';
import ButtonInfoWrapper from '../components/Molecules/ButtonInfoWrapper';

const Container = styled.div`
  min-height: 100vh;
  width: 100%;
`;

const OceanContainer = styled(Container)`
  background-image: url(${Ocean});
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-left: 10%;
  padding-top: 10%;

  @media (max-width: 1379px) {
    justify-content: center;
    padding: 0;
  }
`;

const Services = () => (
  <>
    <Container id="services">
      <ServiceWrapper
        photo={ServicePhoto1}
        heading="Homes"
      />
    </Container>
    <ManufacturerWrapper />
    <Container>
      <ServiceWrapper
        photo={ServicePhoto2}
        change="second"
        heading="Companies"
      />
      <Line />
    </Container>
    <OceanContainer>
      <ButtonInfoWrapper
        change="ocean"
        heading="Save nature"
      />
    </OceanContainer>
  </>
);
export default Services;
