import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const StyleButton = styled.button`
  width: 233px;
  height: 63px;
  background-color: ${({ theme }) => theme.mainBlue};
  border-radius: 50px;
  border: none;
  transition: all 0.5s;
  cursor: pointer;
  margin: 10px 0;

  :focus {
    outline: none;
  }

  @media (max-width: 610px) {
    width: 180px;
    height: 45px;
  }
  @media (max-width: 400px) {
    width: 150px;
  }
`;

const StyleSendButton = styled(StyleButton)`
  border-radius: 11px;
`;

const InnerText = styled.span`
  font-family: ${({ theme }) => theme.fontFamily.Oswald};
  font-weight: 600;
  color: white;
  font-size: ${({ theme, send }) =>
    send ? theme.fontSize.m : theme.fontSize.xs};
  letter-spacing: 2px;
  position: relative;
  transition: 0.3s;
  ::after {
    content: '»';
    position: absolute;
    opacity: 0;
    right: -20px;
    transition: 0.3s;
  }

  :hover {
    padding-right: ${({ send }) =>
      send ? '40px' : '30px'};
    padding-left: 15px;
  }

  :hover:after {
    opacity: 1;
    right: 10px;
  }

  @media (max-width: 610px) {
    font-size: ${({ theme, send }) =>
      send ? theme.fontSize.m : theme.fontSize.xxs};
  }
`;

const Button = ({ send }) => (
  <>
    {send ? (
      <StyleSendButton type="reset">
        <InnerText send>Send</InnerText>
      </StyleSendButton>
    ) : (
      <StyleButton>
        <InnerText>Read more</InnerText>
      </StyleButton>
    )}
  </>
);

Button.propTypes = {
  send: PropTypes.bool,
};

export default Button;
