import React from 'react';
import styled from 'styled-components';
import HomeInfoWrapperBack from '../assets/HomeInfoWrapperBack.svg';
import MainTitle from '../components/Atoms/MainTitle';
import CardWrapper from '../components//Organisms/CardWrapper';
import HomeInfo from '../components/Molecules/HomeInfo';
import HomeBack from '../assets/HomeBack.png';
import InfoWrapper from '../components/Molecules/HomeInfoWrapper';

const HomeContainer = styled.div`
  width: 100%;
  height: 100vh;
  background-image: url(${HomeBack});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const InfoContainer = styled.div`
  min-height: 100vh;
`;

const InfoContainer2 = styled(InfoContainer)`
  background-color: ${({ theme }) => theme.lightGrey};
  background-image: url(${HomeInfoWrapperBack});
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 880px) {
    padding: 0;
    align-items: center;
  }
`;

const HomeTemplate = () => (
  <>
    <HomeContainer id="test">
      <MainTitle />
    </HomeContainer>
    <InfoContainer>
      <HomeInfo />
      <CardWrapper />
    </InfoContainer>
    <InfoContainer2>
      <InfoWrapper />
    </InfoContainer2>
  </>
);

export default HomeTemplate;
