import React, { useState } from 'react';
import styled from 'styled-components';
import HeadingTwoFaces from '../Atoms/HeadingTwoFaces';
import Line from '../Atoms/Line';

const Container = styled.div`
  width: 100%;
  height: 80%;
  font-family: ${({ theme }) => theme.fontFamily.Roboto};
  font-size: ${({ theme }) => theme.fontSize.m};
  font-weight: 600;
  display: grid;
  grid-template-rows: 0.7fr 0.5fr 0.1fr 2fr 0.1fr 0.5fr;
  text-align: center;
  justify-items: center;
  align-items: center;

  @media (max-width: 500px) {
    font-size: ${({ theme }) => theme.fontSize.s};
  }
  @media (max-width: 400px) {
    font-size: ${({ theme }) => theme.fontSize.xs};
  }
`;

const InOutTextContainer = styled.div`
  padding: 20px 0;
`;

const InputValue = styled.input`
  -webkit-outer-spin-button,
  -webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  -moz-appearance: textfield;
  text-align: center;
  width: 15%;
  border: none;
  margin: 0 10px;
  border-bottom: 3px solid ${({ theme }) => theme.mainBlue};
  font-size: ${({ theme }) => theme.fontSize.s};

  @media (max-width: 500px) {
    font-size: ${({ theme }) => theme.fontSize.xs};
  }

  :focus {
    outline: none;
  }
`;

const CalculatorWrapper = () => {
  const [MonthlyCost, changeCost] = useState(0);

  const TakeData = (e) => {
    // const inputValue = parseInt(e.target.value);
    // adding "number" type, NaN is mother*ucker
    changeCost(e.target.value);
  };

  return (
    <Container>
      <div>
        <HeadingTwoFaces
          firstW="Fotovoltaic"
          secondW="Calculator"
        />
      </div>
      <InOutTextContainer>
        Monthly energy cost
        <InputValue
          onChange={TakeData}
          type="number"
          min="0"
        ></InputValue>
        PLN/Month
      </InOutTextContainer>
      <Line change="calc" />
      <div>
        <p>
          Annual electricity cost: {MonthlyCost * 12} PLN
        </p>
        <p>
          kWh per month: {Math.ceil(MonthlyCost * 1.665)}{' '}
          kWh
        </p>
        <p>
          kWh per year:{' '}
          {Math.ceil(MonthlyCost * 1.665) * 12} kWh
        </p>
      </div>
      <Line change="calc" />
      <InOutTextContainer>
        Installation power required:{' '}
        {Math.ceil(
          (Math.ceil(MonthlyCost * 1.665) * 12 * 1.2) /
            1000,
        )}{' '}
        kW
      </InOutTextContainer>
    </Container>
  );
};

export default CalculatorWrapper;
