import React from 'react';
import styled, { css } from 'styled-components';
import ButtonInfoWrapper from '../Molecules/ButtonInfoWrapper';
import PropTypes from 'prop-types';

const Container = styled.div`
  padding: 10px;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
`;

const Picture = styled.img`
  width: 600px;
  height: 560px;
  box-shadow: 0px 0px 15px 10px rgba(0, 0, 0, 0.25);

  @media (max-width: 1319px) {
    width: 600px;
    height: 460px;
  }

  @media (max-width: 740px) {
    width: 500px;
    height: 400px;
    box-shadow: 0px 0px 15px 4px rgba(0, 0, 0, 0.2);
  }
  @media (max-width: 550px) {
    width: 300px;
    height: 300px;
  }

  ${({ change }) =>
    change === 'second' &&
    css`
      order: -1;

      @media (max-width: 1299px) {
        order: 0;
      }
    `}
`;

const ServiceWrapper = ({ photo, change, heading }) => (
  <Container data-aos="fade-right" change={change}>
    <ButtonInfoWrapper heading={heading} change={change} />
    <Picture src={photo} change={change} />
  </Container>
);

ServiceWrapper.propTypes = {
  photo: PropTypes.string,
  change: PropTypes.string,
  heading: PropTypes.string,
};
export default ServiceWrapper;
